import React from 'react';
import { AlertColor } from '@mui/material/Alert/Alert';

export const LangPriorities = ['EN', 'UA', 'ES', 'RU'] as const;

export const PersonTypes = [
  'Owner',
  'OwnerDriver',
  'Coordinator',
  'CoordinatorDriver',
  'Driver',
] as const;

export const UserRoles = ['Admin', 'Super Admin'] as const;

export const TruckTypes = [
  'Cargo van',
  'Reefer van',
  'Box truck',
  'Box truck Reefer',
  'Straight truck',
  'Hotshot',
  'Tented box',
] as const;

export const TruckStatuses = [
  'Available',
  'Not Available',
  'Will be available',
  'On route',
] as const;

export const TruckCrossborders = ['Yes', 'No'] as const;

export const TruckCertificates = [
  'Hazmat',
  'Tsa',
  'TWIC',
  'Tanker Endorsement',
] as const;

export const TruckEquipments = [
  'Dock height risers',
  'Air ride',
  'Lift gate',
  'Keep from freezing',
  'ICC bar',
  'Vertical E-track',
  'Horizontal E-track',
  'Pallet jack',
  'PPE',
  'Ramps',
  'Straps',
  'Loads bars',
  'Blankets',
  'Pads',
  'Fire extinguisher',
  'Metal hooks',
  'Reefer',
  'Heater',
] as const;

export type Order = 'asc' | 'desc';
export type QueryKey<T> = readonly [
  key: string,
  page: number,
  rowsPerPage: number,
  orderBy?: string,
  order?: Order,
  queryParams?: T,
];
// export type BaseQuery = {
//   readonly queryKey: QueryKey;
// };
export type Query<T> = {
  readonly queryKey: QueryKey<T>;
};
export type QuerySingleKey = readonly [key: string, recordId: string];
export type QuerySingle = {
  readonly queryKey: QuerySingleKey;
};
export type Result<T> = {
  items: T[];
  limit: number;
  offset: number;
  total: number;
};
export type QueryFn = (query: Query<{ search: any }>) => Promise<Result<any>>;

export type UserDataType = {
  username: string;
  password: string;
} & Partial<User>;
export interface AuthContextType {
  user: UserDataType | null;
  signin: (
    username: string,
    password: string,
    callback: (user: UserDataType | null) => void,
  ) => void;
  signout: (callback: VoidFunction) => void;
}
export type SnackbarMessage = {
  message: string;
  severity: AlertColor;
  handleClose?: (event?: React.SyntheticEvent | Event, reason?: string) => void;
};
export type PersonType = (typeof PersonTypes)[number];
export type LangPriority = (typeof LangPriorities)[number];
export type UserRole = (typeof UserRoles)[number];
export type TruckType = (typeof TruckTypes)[number];
export type TruckStatus = (typeof TruckStatuses)[number];
export type TruckCrossborder = (typeof TruckCrossborders)[number];
export type TruckCertificate = (typeof TruckCertificates)[number];
export type TruckEquipment = (typeof TruckEquipments)[number];

export type GeoPoint = [number, number];

