import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Person4Icon from '@mui/icons-material/Person4';
import Divider from '@mui/material/Divider';

import {
  DateField,
  Loader,
  SearchField,
  SelectField,
  TextField,
} from '../components';

import { DriverUpdate, DriverQuery, OwnersQuery } from '../api';
import { useSnackbar } from '../hooks';
import { UpdateDriver, LangPriorities, Owner } from '../utils/dataTypes';
import { getType, cleanObject } from '../utils/queryHelpers';

const validationSchema = yup.object({
  fullName: yup.string().required('First name/Last name is required'),
  birthDate: yup
    .date()
    .typeError('Enter correct date')
    .required('Birth Date is required'),
  citizenship: yup.string(),
  languagePriority: yup.string().oneOf(LangPriorities),
  driverLicenceNumber: yup
    .string()
    .required('Driver Licence Number is required'),
  driverLicenceState: yup.string().required('Driver Licence State is required'),
  driverLicenceExp: yup
    .date()
    .typeError('Enter correct date')
    .required('Driver Licence Exp is required'),
  idDocId: yup.string(),
  idDocType: yup.string(),
  idDocExp: yup.date().typeError('Enter correct date'),
  hiredBy: yup.string(),
  hireDate: yup.date().typeError('Enter correct date'),
  address: yup.string(),
  phone: yup.string().required('Phone is required'),
  phone2: yup.string(),
  email: yup.string().email('Enter a valid email'),
  emergencyContactName: yup.string(),
  emergencyContactRel: yup.string(),
  emergencyContactPhone: yup.string(),
  appLogin: yup.string(),
  appPass: yup.string(),
  owner: yup
    .object({ id: yup.string().required() })
    .required('Owner is required'),
  notes: yup.string(),
});

const initialValues = {
  id: '',
  fullName: undefined,
  birthDate: undefined,
  citizenship: undefined,
  languagePriority: undefined,
  driverLicenceNumber: undefined,
  driverLicenceState: undefined,
  driverLicenceExp: undefined,
  idDocId: undefined,
  idDocType: undefined,
  idDocExp: undefined,
  hiredBy: undefined,
  hireDate: undefined,
  address: undefined,
  phone: undefined,
  phone2: undefined,
  email: undefined,
  emergencyContactName: undefined,
  emergencyContactRel: undefined,
  emergencyContactPhone: undefined,
  appLogin: undefined,
  appPass: undefined,
  owner: null,
  notes: undefined,
};

export default function EditDriverForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['driver', pathname.split('/')[3]],
    queryFn: DriverQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const { mutate, isLoading } = useMutation(DriverUpdate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Saved',
      });
      queryClient.invalidateQueries(['drivers']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateDriver>({
    initialValues: {
      ...initialValues,
      ...cleanObject(data, Object.keys(initialValues)),
    },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as UpdateDriver);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box sx={{ height: '100%' }}>
      <form
        onSubmit={formik.handleSubmit}
        onReset={formik.handleReset}
        style={{ height: '100%' }}
      >
        <Box sx={{ p: 3, display: 'grid', height: '100%' }}>
          <Divider />
          <Box
            key="controls"
            sx={{
              height: '100%',
              overflowX: 'hidden',
              overflowY: 'scroll',
            }}
          >
            <Box key="topControls" sx={{ p: 1, paddingBottom: 0 }}>
              <TextField
                fieldName="fullName"
                label="First name/Last name"
                formik={formik}
              />
            </Box>
            <Box
              key="centerControls"
              sx={{ display: 'inline-flex', width: '100%' }}
            >
              <Box
                key="left"
                sx={{ p: 1, paddingTop: 0, paddingBottom: 0, width: '50%' }}
              >
                <DateField
                  fieldName="birthDate"
                  label="Birth Date"
                  formik={formik}
                />
                <TextField
                  fieldName="driverLicenceState"
                  label="Driver Licence State"
                  formik={formik}
                />
                <DateField
                  fieldName="driverLicenceExp"
                  label="Driver Licence Exp"
                  formik={formik}
                />
                <TextField
                  fieldName="idDocType"
                  label="Id Doc Type"
                  formik={formik}
                />
                <TextField
                  fieldName="address"
                  label="Home address"
                  formik={formik}
                />
                <TextField
                  fieldName="hiredBy"
                  label="Hired By"
                  formik={formik}
                />
                <TextField fieldName="email" label="Email" formik={formik} />
                <TextField
                  fieldName="emergencyContactName"
                  label="Emergency Contact Name"
                  formik={formik}
                />
                <TextField
                  fieldName="emergencyContactPhone"
                  label="Emergency Contact Phone"
                  formik={formik}
                />
                <TextField
                  fieldName="appLogin"
                  label="Mobile App Login"
                  formik={formik}
                />
              </Box>
              <Box
                key="right"
                sx={{ p: 1, paddingTop: 0, paddingBottom: 0, width: '50%' }}
              >
                <SelectField
                  fieldName="languagePriority"
                  label="Language Priority"
                  items={LangPriorities}
                  formik={formik}
                />
                <TextField
                  fieldName="citizenship"
                  label="Citizenship"
                  formik={formik}
                />
                <TextField
                  fieldName="driverLicenceNumber"
                  label="Driver Licence Number"
                  formik={formik}
                />
                <TextField
                  fieldName="idDocId"
                  label="Id Doc Id"
                  formik={formik}
                />
                <DateField
                  fieldName="idDocExp"
                  label="Id Doc Exp"
                  formik={formik}
                />
                <DateField
                  fieldName="hireDate"
                  label="Hire Date"
                  formik={formik}
                />
                <TextField fieldName="phone" label="Phone" formik={formik} />
                <TextField fieldName="phone2" label="Phone2" formik={formik} />
                <TextField
                  fieldName="emergencyContactRel"
                  label="Emergency Contact Rel"
                  formik={formik}
                />
                <TextField
                  fieldName="appPass"
                  label="Mobile App Pass"
                  formik={formik}
                />
              </Box>
            </Box>
            <Box
              key="bottomControls"
              sx={{ p: 1, paddingBottom: 0, paddingTop: 0 }}
            >
              <SearchField<Owner>
                fieldName="owner"
                label="Owner"
                getSelectedLabel={(selectedItem) =>
                  `${selectedItem.fullName}, ${getType(selectedItem)}, ${
                    selectedItem.phone
                  }`
                }
                getItemLabel={(item) => `${item.fullName}`}
                getItemSecondLabel={(item) => `${getType(item)}, ${item.phone}`}
                ListIcon={Person4Icon}
                queryFn={OwnersQuery}
                startEmpty={false}
                orderby="fullName"
                order="asc"
                formik={formik}
              />
              <TextField
                fieldName="notes"
                label="Notes"
                multiline
                formik={formik}
              />
            </Box>
          </Box>
          <Divider />
          <Box key="buttons" sx={{ paddingTop: 3, display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
                disabled={!formik.dirty}
              >
                Save
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading || loading || fetching} />
    </Box>
  );
}
